import React, { PureComponent } from 'react';

// import { actionCreators } from './store';
import { connect } from 'react-redux';
import { HomeWrapper } from './style'
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import { CardActionArea } from '@mui/material';
// import { Link } from 'react-router-dom'
import placeholder from "../../assets/images/placeholder.png"
import LazyLoad from 'react-lazyload';

class Profile extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {anchorElNav: ''}
    } 

    handleOpenNavMenu =  (event) => {
        this.setState({
          anchorElNav: event.currentTarget
        })
    }

    handleOpenDoc =  (e) => {
        if(this.props.account){
            this.props.history.push('/renderer/'+ encodeURIComponent(e))
        }else{
            this.setState({
                SnackbarOpen: true,
                message: "Please click the wallet in the upper right corner to login"
            });
        }
    }

    render() {
        const { myNftAssets } = this.props
        return (
            <HomeWrapper>
                <Grid
                container
                direction="row"
                justifyContent="flex-start"
                alignItems="flex-start"
                className='cl-grid'
                >
                { myNftAssets? myNftAssets.map((item, key) => (  
                   
                    <Grid item xs={2} sm={2} md={3} key={key} className="cl-card" onClick={()=>this.handleOpenDoc(item.token_id)}>
                        
                          <Card sx={{ maxWidth: 345 }}>
                         
                {(() => {
                        if (item.metadata) {
                            return (
                                <CardActionArea>
                                    <LazyLoad className='cl-images'>
                                        <img src={item.metadata.image} alt={item.metadata.name}  />
                                    </LazyLoad>
                                    <CardContent>
                                    <Typography gutterBottom variant="h6" component="div" className='cl-nftname'>
                                    {item.metadata.name}
                                    </Typography>
                                    <Typography variant="body2" color="text.secondary" className='cl-description'>
                                    {item.metadata.description}
                                    </Typography>
                                    </CardContent>
                                 </CardActionArea>
                            )
                        } else {
                            return (
                                <CardActionArea>
                                     <LazyLoad className='cl-images'>
                                        <img src={placeholder} alt={"no metadata"}  />
                                    </LazyLoad>
                                    <CardContent>
                                    <Typography gutterBottom variant="h6" component="div" className='cl-nftname'>
                                     no metadata
                                    </Typography>
                                    <Typography variant="body2" color="text.secondary" className='cl-description'>
                                    
                                    </Typography>
                                    </CardContent>
                                </CardActionArea>
                            )
                        }
                    })()}
                      
                        </Card>
                     
                    </Grid>
                    
                 ) ) : ""}
            
                </Grid>
                
            </HomeWrapper>
        )
    }

}

const mapState = (state) => ({
    opensea: state.getIn(['header', 'opensea']),
    chainId: state.getIn(['header', 'chainId']),
    account: state.getIn(['header', 'account']),
    myNftAssets: state.getIn(['header', 'myNftAssets']),
})


export default connect(mapState)(Profile);